@import url(https://fonts.googleapis.com/css?family=Corben);
body, html, #root {
  margin: 0;
  padding: 0;
  height: 100%; }

:root {
  --card-bg-color: white;
  --primary-color: blueviolet; }

#root {
  background-color: blueviolet;
  background-color: var(--primary-color);
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-transition: background-color 0.2s linear;
  transition: background-color 0.2s linear; }
  #root, #root * {
    font-family: Corben, sans-serif; }

.quote-card {
  --card-border-radius: 10px;
  display: flex;
  flex-direction: column;
  background-color: var(--card-bg-color);
  opacity: 0.85;
  border-radius: var(--card-border-radius);
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.2);
  width: 55%;
  min-width: 30vw;
  min-height: 20vh; }
  .quote-card__text {
    flex: 1 1; }
  .quote-card__author {
    font-size: 0.85em;
    font-style: italic;
    text-align: right; }
  .quote-card__text, .quote-card__author {
    padding: 1em; }
  .quote-card .button-group {
    font-size: large; }
    .quote-card .button-group .button:first-child {
      border-bottom-left-radius: var(--card-border-radius); }
    .quote-card .button-group .button:last-child {
      border-bottom-right-radius: var(--card-border-radius); }

.button {
  border: none;
  font-size: 1em;
  cursor: pointer;
  outline: 0;
  -webkit-transition: all linear 0.1s;
  transition: all linear 0.1s; }
  .button__primary {
    color: white;
    background-color: var(--primary-color); }
  .button__tweet {
    --twitter-primary-color: #55acee;
    --twitter-secondary-color: #f5f8fa;
    background-color: var(--card-bg-color); }
    .button__tweet:hover, .button__tweet:focus {
      color: var(--twitter-secondary-color);
      background-color: var(--twitter-primary-color); }

.button-group {
  display: flex; }
  .button-group > .button {
    flex: 1 1; }

.loader {
  --animation-duration: 1s;
  display: inline-block;
  position: relative;
  width: 72px;
  height: 72px; }
  .loader::before, .loader::after {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    left: 50%;
    border: 4px solid #fff;
    border-radius: 50%;
    -webkit-animation: beam var(--animation-duration) cubic-bezier(0, 0.2, 0.8, 1) infinite;
            animation: beam var(--animation-duration) cubic-bezier(0, 0.2, 0.8, 1) infinite; }
  .loader::after {
    -webkit-animation-delay: calc(var(--animation-duration) / 2);
            animation-delay: calc(var(--animation-duration) / 2); }

@-webkit-keyframes beam {
  0% {
    width: 0;
    height: 0; }
  100% {
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    opacity: 0; } }

@keyframes beam {
  0% {
    width: 0;
    height: 0; }
  100% {
    top: 0px;
    left: 0px;
    width: 100%;
    height: 100%;
    opacity: 0; } }

